button {
  min-width: 100px;
  text-transform: uppercase;
  background: #e49916;
  border: 0;
  border-radius: 4px;
  margin-left: 16px;
  margin-right: auto;
  padding: 8px 16px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 500;
  transition: background .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

